@import "../../global.scss";

.portfolio{
    background-color:white;
    display: flex;
    flex-direction: column;
    align-items:center;

    h1{
        font-size: 50px;
    }
    ul{
        margin: 10px;
        padding: 0%;
        list-style: none;
        display: flex;
        
    
    }
    .container{
        width:70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .item{
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid lightgrey;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color:white;
            position: relative;
            transition: all 0.5s ease;

        
        h3{
            position: absolute;
            font-size: 20px;
        }
        img{
            width: 100%;
            height:100%;
            object-fit: cover;
            z-index: 1;
        }
        &:hover{
            background-color: $main-color;
            img{
                opacity: 0.2;
                z-index: 0;
            }
           
        }
    }
}

}